
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  margin: 0;
  padding: 0;
  background-color: #212121;
}
h1,
h2 {
  font-weight: normal;
}
.logo {
  position: fixed;
  left: 12px;
  top: 0px;
  height: 50px;
  margin-top: 8px;
}
nav {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  background-color: #000000;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin: 0;
  position: fixed;
  top: 0;
}
a {
  color: #de771c;
  text-decoration: none;
}
nav a {
  padding: 20px;
}
nav a:hover {
  color: white;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  margin: 0;
  padding: 0;
  display: block;
  height: 100vh;
  overflow: auto;
  background-size: cover;
  background-attachment: fixed;
}
.background {
  background: url(https://image.ibb.co/gEpidT/bg4.jpg);
  z-index: -2;
}
.container {
  margin: 0;
  width: 100vw;
  padding-top: 60px;
  position: relative;
  /* background-color: rgba(0, 60, 70, 0.4); */
  /* height: 100vh;
  overflow: auto; */
}
.background:before {
  pointer-events: none;
  display: block;
  position: fixed;
  content: ' ';
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 60, 70, 0.4);
  z-index: 0;
}
.content .center-img {
  width: 60vw;
}
.content {
  width: 60vw;
  max-width: 1000px;
  margin: auto;
}
.member {
  clear: both;
  margin-bottom: 20px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
@media (max-width: 800px) {
.content {
    width: 90vw;
}
.content .center-img {
    width: 90vw;
}
iframe {
    width: 90vw;
}
.member {
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}
nav {
    padding-top: 50px;
    -webkit-box-pack: start;
        -ms-flex-pack: start;
            justify-content: flex-start;
    left: 0;
    overflow: auto;
}
nav a {
    padding: 10px;
}
.container {
    padding-top: 90px;
}
}
.member img {
  -ms-flex-negative: 0;
      flex-shrink: 0;
  -ms-flex-item-align: center;
      align-self: center;
  width: 200px !important;
  height: 200px !important;
  margin-bottom: 20px;
  margin: 10px;
  display: block;
}






























































































